.header {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 12px;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  align-items: center;
  background-color: rgba(85, 26, 139, 255);
}

.menu {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
}

.menu-icon {
  font-size: 26px;
  display: flex;
  color: white;
}

.mobile-menu {
  display: none;
}

.web-menu {
  display: flex;
}

.name {
  display: flex;
  font-size: 20px;
  color: white;
}

.name a {
  color: white;
}

@media only screen and (max-width: 720px) {
  .mobile-menu {
    display: flex;
  }
  .web-menu {
    display: none;
  }
  .name {
    font-size: 18px;
    color: white;
  }
}
