body {
  margin: 0;
  font-family: "Nunito", "Fira Sans", "monospace", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Nunito", "Fira Sans", "monospace", source-code-pro, Menlo,
    Monaco, Consolas, "Courier New";
}

a:link {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}

.header {
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  padding: 12px;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  align-items: center;
  background-color: rgba(85, 26, 139, 255);
}

.menu {
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
  align-items: center;
}

.menu-icon {
  font-size: 26px;
  display: flex;
  color: white;
}

.mobile-menu {
  display: none;
}

.web-menu {
  display: flex;
}

.name {
  display: flex;
  font-size: 20px;
  color: white;
}

.name a {
  color: white;
}

@media only screen and (max-width: 720px) {
  .mobile-menu {
    display: flex;
  }
  .web-menu {
    display: none;
  }
  .name {
    font-size: 18px;
    color: white;
  }
}

.web {
  display: flex;
  flex: 1 1;
  align-items: center;
}

.web-option {
  font-size: 20px;
  margin-right: 32px;
  font-weight: 600;
}

.web-option a {
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
}

a.single-web-option:hover {
  font-style: italic;
}

:root {
  --primary-color: #5d2591;
}

.mobile {
  height: 100vh;
  width: 60vw;
  position: absolute;
  right: 0px;
  padding: 30px 12px;
  top: -6px;
  display: flex;
  flex-direction: column;
  background-color: #5d2591;
  background-color: var(--primary-color);
}

.close-icon {
  font-size: 20px;
  display: flex;
  justify-content: flex-end;
  color: white;
}

.mobile-options {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 0px;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.mobile-option {
  font-size: 20px;
  margin: 36px 8px;
  display: flex;
}

.mobile-option a {
  color: white;
  display: flex;
  font-weight: 600;
}

.body {
  padding: 36px 12px;
}

p {
  font-size: 18px;
}

.social-contact {
  display: flex;
  flex-direction: row;
  padding: 20px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: 30px;
  gap: 30px;
}

.social-icon {
  color: rgba(85, 26, 139, 255);
}

a.social-icon {
  margin: 0 1rem;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  display: inline-block;
}

a.social-icon:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

@media only screen and (max-width: 370px) {
  .social-contact {
    grid-gap: 15px;
    gap: 15px;
  }
}

.about-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 320px;
  padding: 16px;
}

.about-info {
  font-size: 22px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  grid-gap: 14px;
  gap: 14px;
}

.education {
  font-style: italic;
  color: rgb(169, 91, 169);
  font-weight: 600;
}

.info-name {
  font-size: 54px;
  font-weight: 600;
  font-family: "Fira Mono", "Fira Sans", monospace;
  color: rgba(85, 26, 139, 255);
}

.hello {
  font-family: "Fira Mono", "Fira Sans", monospace;
  font-weight: 500;
}

.statement {
  padding-top: 24px;
  font-family: "Fira Mono", "Fira Sans", monospace;
  font-weight: 500;
}

@media only screen and (max-width: 1096px) {
  .info-name {
    font-size: 38px;
  }
}

@media only screen and (max-width: 720px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .info-name {
    font-size: 34px;
  }
  .statement {
    font-size: 18px;
  }
  .hello {
    font-size: 18px;
  }
  .education {
    font-size: 18px;
  }
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 300px;
  }
}

@media only screen and (max-width: 430px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 300px;
  }
  .info-name {
    font-size: 34px;
    font-weight: 600;
    font-family: "Fira Mono", "Fira Sans", monospace;
    color: rgba(85, 26, 139, 255);
  }
}

@media only screen and (max-width: 360px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picture {
    height: 250px;
  }
}

.project-card {
  display: flex;
  flex: 40% 1;
  flex-direction: column;
  max-width: 44%;
  align-items: center;
  margin: 16px 0px;
  padding: 30px 20px;
  border: 3px solid rgb(212, 194, 212);
  border-radius: 10px;
  grid-gap: 10px;
  gap: 10px;
}

.project-card:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: all 0.5s ease;
  box-shadow: 0 10px 40px 0 rgba(40, 39, 39, 0.4);
  z-index: 3;
}

.project-title {
  font-size: 24px;
  font-weight: 600;
}

.project-photo {
  width: 100%;
  max-height: 70%;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 16px;
  margin-top: 16px;
}

.tag {
  padding: 4px 8px;
  background-color: rgb(125, 81, 125);
  color: white;
  border-radius: 8px;
  margin-right: 8px;
  margin-bottom: 4px;
  font-size: 14px;
}

.fi-brands-github {
  margin-right: 5px;
}

.fi-rr-globe {
  margin-right: 5px;
}

.project-links a {
  color: black;
}

.project-links {
  display: flex;
  flex-direction: row;
}

.link-button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  font-size: 16px;
  margin-right: 20px;
}

.link-button i {
  display: flex;
  align-items: center;
  -webkit-mask-repeat: 4px;
          mask-repeat: 4px;
}

.section-title {
  font-size: 40px;
  font-weight: 700;
  color: rgb(88, 38, 135);
}

@media only screen and (max-width: 730px) {
  .project-card {
    flex: 100% 1;
    max-width: 100%;
  }
}
@media only screen and (max-width: 720px) {
  .project-photo {
    width: 100%;
  }
  .project-card {
    flex-direction: column;
    border: 3px solid rgb(205, 182, 207);
  }
  .project-info {
    grid-gap: 20px;
    gap: 20px;
  }
  .project-tags {
    max-width: 95%;
  }
  .project-info {
    max-width: 95%;
  }
}

.project-card-container {
  scroll-behavior: smooth;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap: 2em;
  gap: 2em;
  justify-content: space-between;
  padding: 10px 0px;
}

@media only screen and (max-width: 730px) {
  .project-card-container {
    display: flex;
    flex-direction: column;
    grid-gap: 2em;
    gap: 2em;
  }
}

:root {
  --primary-color: #5d2591;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
}

/* Timeline Container */
.timeline {
  margin: 20px auto;
  padding: 20px;
}

/* Outer Layer with the timeline border */
.outer {
  border-left: 2px solid #5d2591;
  border-left: 2px solid var(--primary-color);
}

/* Card container */
.card {
  position: relative;
  margin: 0 0 20px 20px;
  padding: 10px;
  color: rgb(21, 21, 21);
  border-radius: 8px;
  max-width: 80%;
}

/* Information about the timeline */
.info {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

/* Title of the card */
.title {
  color: #5d2591;
  color: var(--primary-color);
  position: relative;
}

/* Timeline dot  */
.title::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 999px;
  left: -39px;
  border: 3px solid #5d2591;
  border: 3px solid var(--primary-color);
}

:root {
  --primary-color: #5d2591;
}

.contact {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.contact-container {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  justify-content: space-between;
  align-items: center;
}

.contact-left {
  max-width: 60%;
}
.quote {
  text-align: center;
  font-size: 22px;
  color: rgb(129, 95, 152);
}

.download {
  background-color: #5d2591;
  background-color: var(--primary-color);
  padding: 10px 16px;
  font-size: 24px;
  display: flex;
  align-items: center;
  border-radius: 4px;
}

.download a {
  text-decoration: none;
  color: white;
}

.download-link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 0.5em;
  gap: 0.5em;
}

a.download-link:hover {
  font-style: italic;
}

.about-detail {
  display: flex;
  flex-direction: column;
  padding: 30px 0px;
}
.section-title {
  padding: 10px 0px;
}

p {
  margin: 10px 0px;
  line-height: 1.6;
}

html {
  scroll-behavior: smooth;
}
.home {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
}

@media only screen and (max-width: 1024px) {
  .home {
    width: 90%;
  }
}

@media only screen and (max-width: 512px) {
  .home {
    width: 100%;
  }
}

